//  pixiv 
//  className
//  className  className
class ThemeColor {
    constructor() {
        this.selector = '#gtm-var-theme-kind'; // 
        this.timer = 0;
        this._theme = ''; // 
        //  className
        this.colorMap = new Map([['dark', 'theme-dark']]);
        this.elList = []; // 
        // 
        this.timer = window.setTimeout(() => {
            this.findFlag();
        }, 300);
    }
    // 
    findFlag() {
        const el = document.querySelector(this.selector);
        if (el) {
            window.clearTimeout(this.timer);
            this.theme = el.textContent;
            //  textContent 
            const ob = new MutationObserver((mutationsList) => {
                for (const item of mutationsList) {
                    if (item.type === 'characterData') {
                        this.theme = item.target.nodeValue;
                        break;
                    }
                }
            });
            ob.observe(el, {
                characterData: true,
                subtree: true,
            });
        }
    }
    set theme(flag) {
        if (!flag) {
            return;
        }
        this._theme = flag;
        for (const el of this.elList) {
            this.setClass(el);
        }
    }
    get theme() {
        return this._theme;
    }
    // 
    register(el) {
        this.elList.push(el);
        this.setClass(el);
    }
    //  className
    setClass(el) {
        //  className
        for (const className of this.colorMap.values()) {
            if (el.classList.contains(className)) {
                el.classList.remove(className);
            }
        }
        //  className
        const name = this.colorMap.get(this._theme);
        name && el.classList.add(name);
    }
}
const themeColor = new ThemeColor();
export { themeColor };
