import { API } from '../API';
class MakeEPUB {
    constructor() { }
    // epub  title  epub 
    make(data, content = '') {
        return new Promise((resolve, reject) => {
            content = content.replace(/\n/g, '<br/>');
            new EpubMaker()
                .withTemplate('idpf-wasteland')
                .withAuthor(API.replaceUnsafeStr(data.body.userName))
                .withModificationDate(new Date(data.body.createDate))
                .withRights({
                description: data.body.description,
                license: '',
            })
                .withAttributionUrl(`https://www.pixiv.net/novel/show.php?id=${data.body.id}`)
                .withCover(data.body.coverUrl, {
                license: '',
                attributionUrl: '',
            })
                .withTitle(API.replaceUnsafeStr(data.body.title))
                .withSection(new EpubMaker.Section('1', null, { content: content }, false, true))
                .makeEpub()
                .then((blob) => {
                resolve(blob);
            });
        });
    }
}
const makeEPUB = new MakeEPUB();
export { makeEPUB };
