//
import { InitPageBase } from '../InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { options } from '../Options';
import { store } from '../Store';
import { QuickBookmark } from '../QuickBookmark';
import { DOM } from '../DOM';
import { API } from '../API';
import { log } from '../Log';
import { EVT } from '../EVT';
import { SaveAvatarIcon } from '../SaveAvatarIcon';
class InitNovelPage extends InitPageBase {
    constructor() {
        super();
        this.crawlDirection = 0; // 
        this.quickDownBtn = document.createElement('div');
        this.init();
    }
    initElse() {
        this.initQuickBookmark();
        window.addEventListener(EVT.events.pageSwitch, this.initQuickBookmark);
    }
    initQuickBookmark() {
        new QuickBookmark();
    }
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_new')).addEventListener('click', () => {
            this.crawlDirection = -1;
            this.readyCrawl();
        });
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_old')).addEventListener('click', () => {
            this.crawlDirection = 1;
            this.readyCrawl();
        });
        const btn = DOM.addBtn('otherBtns', Colors.green, lang.transl('_'), [
            ['title', lang.transl('_')],
        ]);
        new SaveAvatarIcon(btn);
    }
    appendElseEl() {
        // 
        this.quickDownBtn.id = 'quick_down_btn';
        this.quickDownBtn.textContent = '';
        this.quickDownBtn.setAttribute('title', lang.transl('_'));
        document.body.appendChild(this.quickDownBtn);
        this.quickDownBtn.addEventListener('click', () => {
            store.states.quickDownload = true;
            this.readyCrawl();
        }, false);
    }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_') +
                '<br>' +
                lang.transl('_0'),
            rangTip: lang.transl('_1'),
            value: '-1',
        });
    }
    destroy() {
        DOM.clearSlot('crawlBtns');
        DOM.clearSlot('otherBtns');
        // 
        DOM.removeEl(this.quickDownBtn);
        window.removeEventListener(EVT.events.pageSwitch, this.initQuickBookmark);
    }
    /*
    -1 
    0 
    1 
    */
    getWantPage() {
        if (store.states.quickDownload) {
            // 
            this.crawlNumber = 1;
        }
        else {
            // 
            const crawlAllTip = this.crawlDirection === -1
                ? lang.transl('_new')
                : lang.transl('_old');
            this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), crawlAllTip);
        }
    }
    nextStep() {
        if (store.states.quickDownload) {
            // 
            store.idList.push({
                type: 'novels',
                id: API.getNovelId(window.location.href),
            });
            log.log(lang.transl('_'));
            this.getIdListFinished();
        }
        else {
            // 
            this.getIdList();
        }
    }
    async getIdList() {
        let type = ['novels'];
        let idList = await API.getUserWorksByType(DOM.getUserId(), type);
        //  id
        let nowId = parseInt(API.getIllustId(window.location.href));
        idList.forEach((id) => {
            let idNum = parseInt(id.id);
            // 
            if (idNum >= nowId && this.crawlDirection === -1) {
                store.idList.push(id);
            }
            else if (idNum <= nowId && this.crawlDirection === 1) {
                // 
                store.idList.push(id);
            }
        });
        // 
        if (this.crawlNumber !== -1) {
            //  
            if (this.crawlDirection === -1) {
                store.idList.sort(API.sortByProperty('id')).reverse();
            }
            else {
                //  
                store.idList.sort(API.sortByProperty('id'));
            }
            store.idList = store.idList.splice(0, this.crawlNumber);
        }
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.crawlDirection = 0; // 
    }
}
export { InitNovelPage };
