import { API } from './API';
import { DOM } from './DOM';
import { Colors } from './Colors';
import { lang } from './Lang';
import { form } from './Settings';
// 
class BookmarkAllWorks {
    constructor() {
        this.type = 'illusts'; // 
        this.idList = [];
        this.addTagList = []; //  tag 
        this.index = 0;
        this.workList = null;
        this.token = API.getToken();
        const btn = DOM.addBtn('otherBtns', Colors.green, lang.transl('_'));
        this.btn = btn;
    }
    // workList  id
    setWorkList(list) {
        if (!list) {
            alert(lang.transl('_'));
            return;
        }
        this.workList = list;
        this.readyAddTag();
    }
    //  tag
    async readyAddTag(loop = 0) {
        // 
        this.idList = [];
        this.addTagList = [];
        this.index = 0;
        this.token = API.getToken();
        this.btn.setAttribute('disabled', 'disabled');
        this.btn.textContent = `Checking`;
        if (window.location.pathname.includes('/novel')) {
            this.type = 'novels';
        }
        this.getIdList();
    }
    //  id
    getIdList() {
        if (!this.workList) {
            return;
        }
        const regExp = this.type === 'illusts' ? /\/artworks\/(\d*)/ : /\?id=(\d*)/;
        for (const el of this.workList) {
            const a = el.querySelector('a');
            if (a) {
                // "https://www.pixiv.net/artworks/82618568"
                // "https://www.pixiv.net/novel/show.php?id=12350618"
                const test = regExp.exec(a.href);
                if (test && test.length > 1) {
                    this.idList.push(test[1]);
                }
            }
        }
        this.getTagData();
    }
    //  tag
    async getTagData() {
        this.btn.textContent = `Get data ${this.index} / ${this.idList.length}`;
        const id = this.idList[this.index];
        try {
            let data;
            // 
            if (this.type === 'novels') {
                data = await API.getNovelData(id);
            }
            else {
                data = await API.getArtworkData(id);
            }
            const tagArr = data.body.tags.tags; //  tag 
            const tags = []; //  tag 
            for (const tagData of tagArr) {
                tags.push(tagData.tag);
            }
            this.addTagList.push({
                id: data.body.id,
                tags: tags,
                restrict: false,
            });
            this.index++;
            if (this.index === this.idList.length) {
                this.index = 0;
                return this.addTag();
            }
            this.getTagData();
        }
        catch (error) {
            this.getTagData();
        }
    }
    //  tag
    async addTag() {
        this.btn.textContent = `Add bookmark ${this.index} / ${this.idList.length}`;
        const data = this.addTagList[this.index];
        //  tag 
        if (form.quickBookmarks.checked === false) {
            data.tags = [];
        }
        await API.addBookmark(this.type, data.id, data.tags, data.restrict, this.token);
        this.index++;
        // 
        if (this.index === this.addTagList.length) {
            this.btn.textContent = ` Complete`;
            this.btn.removeAttribute('disabled');
            return;
        }
        // 
        this.addTag();
    }
}
export { BookmarkAllWorks };
