//
import { InitPageBase } from '../InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { options } from '../Options';
import { store } from '../Store';
import { DOM } from '../DOM';
import { API } from '../API';
class InitNovelSeriesPage extends InitPageBase {
    constructor() {
        super();
        this.seriesId = '';
        this.limit = 30;
        this.last = 0;
        this.init();
    }
    initElse() { }
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_')).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    appendElseEl() { }
    setFormOption() {
        // /
        options.hideOption([1]);
    }
    getWantPage() { }
    nextStep() {
        this.seriesId = API.getURLPathField('series');
        this.getIdList();
    }
    async getIdList() {
        const seriesData = await API.getNovelSeriesData(this.seriesId, this.limit, this.last, 'asc');
        const list = seriesData.body.seriesContents;
        for (const item of list) {
            store.idList.push({
                type: 'novels',
                id: item.id,
            });
        }
        this.last += list.length;
        // 
        if (list.length === this.limit) {
            this.getIdList();
        }
        else {
            this.getIdListFinished();
        }
    }
    resetGetIdListStatus() {
        this.seriesId = '';
        this.last = 0;
    }
}
export { InitNovelSeriesPage };
