//   artwork 
import { InitPageBase } from '../InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { DOM } from '../DOM';
import { options } from '../Options';
import { filter } from '../Filter';
import { API } from '../API';
import { store } from '../Store';
import { log } from '../Log';
class InitBookmarkNewArtworkPage extends InitPageBase {
    constructor() {
        super();
        this.r18 = false;
        this.init();
    }
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    appendElseEl() { }
    setFormOption() {
        // /
        this.maxCount = 100;
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: `1 - ${this.maxCount}`,
            value: this.maxCount.toString(),
        });
    }
    getWantPage() {
        const check = this.checkWantPageInputGreater0();
        if (check == undefined) {
            return;
        }
        this.crawlNumber = check;
        if (this.crawlNumber > this.maxCount) {
            this.crawlNumber = this.maxCount;
        }
        log.warning(lang.transl('_1', this.crawlNumber.toString()));
    }
    nextStep() {
        this.r18 = location.pathname.includes('r18');
        const p = API.getURLSearchField(location.href, 'p');
        this.startpageNo = parseInt(p) || 1;
        this.getIdList();
    }
    async getIdList() {
        let p = this.startpageNo + this.listPageFinished;
        // 
        let worksData;
        try {
            worksData = await API.getBookmarkNewIllustData(p, this.r18);
        }
        catch (error) {
            this.getIdList();
            return;
        }
        // 
        for (const data of worksData) {
            const filterOpt = {
                id: data.illustId,
                width: data.width,
                height: data.height,
                pageCount: data.pageCount,
                bookmarkData: data.isBookmarked,
                illustType: parseInt(data.illustType),
                tags: data.tags,
            };
            if (await filter.check(filterOpt)) {
                store.idList.push({
                    type: API.getWorkType(data.illustType),
                    id: data.illustId,
                });
            }
        }
        this.listPageFinished++;
        log.log(lang.transl('_', this.listPageFinished.toString()), 1, false);
        // 
        // 
        if (p >= this.maxCount || this.listPageFinished === this.crawlNumber) {
            log.log(lang.transl('_'));
            this.getIdListFinished();
        }
        else {
            // 
            this.getIdList();
        }
    }
    resetGetIdListStatus() {
        this.listPageFinished = 0;
    }
}
export { InitBookmarkNewArtworkPage };
