// 
import { InitPageBase } from './InitPageBase';
import { API } from './API';
import { Colors } from './Colors';
import { lang } from './Lang';
import { options } from './Options';
import { BookmarksAddTag } from './BookmarksAddTag';
import { store } from './Store';
import { log } from './Log';
import { DOM } from './DOM';
import { pageInfo } from './PageInfo';
class InitBookmarkLegacyPage extends InitPageBase {
    constructor() {
        super();
        this.idList = []; //  id
        this.type = 'illusts'; // 
        this.tag = ''; //  tag
        this.isHide = false; // 
        this.requsetNumber = 0; // 
        this.onceRequest = 100; // 
        this.offset = 0; // 
        this.crawlRecommended = false; // 
        this.init();
    }
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
        // 
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_Title')],
        ]).addEventListener('click', () => {
            this.crawlRecommended = true;
            this.readyCrawl();
        }, false);
        //  token tag
        if (API.getToken()) {
            const btn = DOM.addBtn('otherBtns', Colors.green, lang.transl('_tag'), [['title', lang.transl('_tag')]]);
            new BookmarksAddTag(btn);
        }
    }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: lang.transl('_1'),
            value: '-1',
        });
        // 
        options.hideOption([6]);
    }
    getWantPage() {
        let pageTip = '';
        if (this.crawlRecommended) {
            pageTip = lang.transl('_');
        }
        else {
            pageTip = lang.transl('_');
        }
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), pageTip);
    }
    nextStep() {
        if (window.location.pathname.includes('/novel')) {
            this.type = 'novels';
        }
        if (this.crawlRecommended) {
            // 
            this.getRecommendedList();
        }
        else {
            this.readyGetIdList();
            //  id 
            this.getIdList();
        }
    }
    readyGetIdList() {
        // 
        const onceNumber = 20;
        // 
        const nowPage = API.getURLSearchField(location.href, 'p'); //  1 
        if (nowPage) {
            this.offset = (parseInt(nowPage) - 1) * onceNumber;
        }
        if (this.offset < 0) {
            this.offset = 0;
        }
        // 
        this.requsetNumber = 0;
        if (this.crawlNumber === -1) {
            this.requsetNumber = 9999999;
        }
        else {
            this.requsetNumber = onceNumber * this.crawlNumber;
        }
        this.tag = pageInfo.getPageTag;
        // 
        //  url rest 
        this.isHide = API.getURLSearchField(location.href, 'rest') === 'hide';
        log.log(lang.transl('_'));
        if (this.crawlNumber === -1) {
            log.log(lang.transl('_'));
        }
    }
    // 
    async getIdList() {
        let data;
        try {
            data = await API.getBookmarkData(DOM.getUserId(), this.type, this.tag, this.offset, this.isHide);
        }
        catch (error) {
            this.getIdList();
            return;
        }
        if (data.body.works.length === 0 ||
            this.idList.length >= this.requsetNumber) {
            // 
            return this.afterGetIdList();
        }
        else {
            // 
            const idType = this.type === 'illusts' ? 'unknown' : 'novels';
            data.body.works.forEach((data) => this.idList.push({
                type: idType,
                id: data.id,
            }));
            this.offset += this.onceRequest; // 
            // 
            this.getIdList();
        }
    }
    //  id 
    afterGetIdList() {
        //  100 
        if (this.idList.length > this.requsetNumber) {
            // 
            this.idList.splice(this.requsetNumber, this.idList.length);
            //  api  id 
        }
        store.idList = store.idList.concat(this.idList);
        this.getIdListFinished();
    }
    // 
    getRecommendedList() {
        const selector = this.type === 'illusts'
            ? '#illust-recommend .image-item'
            : '.novel-items>li';
        const idType = this.type === 'illusts' ? 'unknown' : 'novels';
        const getId = this.type === 'illusts' ? API.getIllustId : API.getNovelId;
        // 
        const elements = document.querySelectorAll(selector);
        if (elements.length === 0) {
            this.crawlRecommended = false;
            return this.noResult();
        }
        // 
        for (const li of elements) {
            const a = li.querySelector('a');
            if (store.idList.length === this.crawlNumber) {
                break;
            }
            store.idList.push({
                type: idType,
                id: getId(a.href),
            });
        }
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.type = 'illusts';
        this.idList = [];
        this.offset = 0;
        this.tag = '';
        this.listPageFinished = 0;
        this.crawlRecommended = false; // 
    }
    sortResult() {
        // 
        !this.crawlRecommended && store.result.reverse();
    }
}
export { InitBookmarkLegacyPage };
