// 
import { InitPageBase } from './InitPageBase';
import { API } from './API';
import { Colors } from './Colors';
import { lang } from './Lang';
import { options } from './Options';
import { BookmarksAddTag } from './BookmarksAddTag';
import { store } from './Store';
import { log } from './Log';
import { DOM } from './DOM';
import { pageInfo } from './PageInfo';
class InitBookmarkPage extends InitPageBase {
    constructor() {
        super();
        this.idList = []; //  id
        this.type = 'illusts'; // 
        this.tag = ''; //  tag
        this.isHide = false; // 
        this.requsetNumber = 0; // 
        this.onceRequest = 100; // 
        this.offset = 0; // 
        this.init();
    }
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
        //  token tag
        if (API.getToken()) {
            const btn = DOM.addBtn('otherBtns', Colors.green, lang.transl('_tag'), [['title', lang.transl('_tag')]]);
            new BookmarksAddTag(btn);
        }
    }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: lang.transl('_1'),
            value: '-1',
        });
        // 
        options.hideOption([6]);
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
    }
    nextStep() {
        if (window.location.pathname.includes('/novel')) {
            this.type = 'novels';
        }
        this.readyGetIdList();
        this.getIdList();
    }
    readyGetIdList() {
        // 
        const onceNumber = 48;
        // 
        const nowPage = API.getURLSearchField(location.href, 'p'); //  1 
        if (nowPage) {
            this.offset = (parseInt(nowPage) - 1) * onceNumber;
        }
        if (this.offset < 0) {
            this.offset = 0;
        }
        // 
        this.requsetNumber = 0;
        if (this.crawlNumber === -1) {
            this.requsetNumber = 9999999;
        }
        else {
            this.requsetNumber = onceNumber * this.crawlNumber;
        }
        this.tag = pageInfo.getPageTag;
        // 
        //  url rest 
        this.isHide = API.getURLSearchField(location.href, 'rest') === 'hide';
        log.log(lang.transl('_'));
        if (this.crawlNumber === -1) {
            log.log(lang.transl('_'));
        }
    }
    // 
    async getIdList() {
        let data;
        try {
            data = await API.getBookmarkData(DOM.getUserId(), this.type, this.tag, this.offset, this.isHide);
        }
        catch (error) {
            this.getIdList();
            return;
        }
        if (data.body.works.length === 0 ||
            this.idList.length >= this.requsetNumber) {
            // 
            return this.afterGetIdList();
        }
        else {
            // 
            const idType = this.type === 'illusts' ? 'unknown' : 'novels';
            data.body.works.forEach((data) => this.idList.push({
                type: idType,
                id: data.id,
            }));
            this.offset += this.onceRequest; // 
            // 
            this.getIdList();
        }
    }
    //  id 
    afterGetIdList() {
        //  100 
        if (this.idList.length > this.requsetNumber) {
            // 
            this.idList.splice(this.requsetNumber, this.idList.length);
            //  api  id 
        }
        store.idList = store.idList.concat(this.idList);
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.type = 'illusts';
        this.idList = [];
        this.offset = 0;
        this.tag = '';
        this.listPageFinished = 0;
    }
}
export { InitBookmarkPage };
