// 
import { lang } from './Lang';
import { Colors } from './Colors';
import { DOM } from './DOM';
import { options } from './Options';
import { saveArtworkData } from './artwork/SaveArtworkData';
import { saveNovelData } from './novel/SaveNovelData';
import { filter } from './Filter';
import { API } from './API';
import { store } from './Store';
import { log } from './Log';
import { EVT } from './EVT';
import { setting, form } from './Settings';
import { titleBar } from './TitleBar';
import { pageInfo } from './PageInfo';
class InitPageBase {
    constructor() {
        this.crawlNumber = 0; // /
        this.firstFewImages = 0; // 01p
        this.maxCount = 1000; // /
        this.startpageNo = 1; //  api 
        this.listPageFinished = 0; // 
        this.ajaxThreadsDefault = 10; // 
        this.ajaxThreads = this.ajaxThreadsDefault; // 
        this.ajaxThreadsFinished = 0; //  ajaxThreads 
    }
    init() {
        options.showAllOption();
        this.setFormOption();
        this.appendCenterBtns();
        this.appendElseEl();
        this.initElse();
        window.addEventListener(EVT.events.pageTypeChange, () => {
            this.destroy();
        });
    }
    // 
    initElse() { }
    // 
    destroy() {
        DOM.clearSlot('crawlBtns');
        DOM.clearSlot('otherBtns');
    }
    // 
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    // 
    appendElseEl() { }
    // 
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: lang.transl('_1'),
            value: '1',
        });
    }
    // /
    getWantPageError() {
        EVT.fire(EVT.events.crawlError);
        const msg = lang.transl('_');
        window.alert(msg);
        throw new Error(msg);
    }
    // /
    //  -1 0
    checkWantPageInput(crawlPartTip, crawlAllTip) {
        const temp = parseInt(form.setWantPage.value);
        //  1  -1
        if ((temp < 1 && temp !== -1) || isNaN(temp)) {
            //  1  -1 , 0 
            this.getWantPageError();
        }
        if (temp >= 1) {
            log.warning(crawlPartTip.replace('-num-', temp.toString()));
        }
        else if (temp === -1) {
            log.warning(crawlAllTip);
        }
        return temp;
    }
    // /
    //  0
    checkWantPageInputGreater0() {
        const result = API.checkNumberGreater0(form.setWantPage.value);
        if (result.result) {
            return result.value;
        }
        else {
            this.getWantPageError();
        }
    }
    // 
    getFirstFewImages() {
        const result = setting.getFirstFewImages();
        if (result === undefined) {
            EVT.fire(EVT.events.crawlError);
            const msg = lang.transl('_') + ' ' + lang.transl('_0');
            log.error(msg);
            window.alert(msg);
            throw new Error(msg);
        }
        return result;
    }
    // 
    getWantPage() { }
    //  filter
    getMultipleSetting() {
        // 
        if (form.firstFewImagesSwitch.checked) {
            this.firstFewImages = this.getFirstFewImages();
            log.warning(lang.transl('_n', this.firstFewImages.toString()));
        }
    }
    // 
    async readyCrawl() {
        // 
        if (!store.states.allowWork) {
            window.alert(lang.transl('_2'));
            return;
        }
        EVT.fire(EVT.events.crawlStart);
        log.clear();
        log.success(lang.transl('_0'));
        titleBar.change('');
        this.getWantPage();
        this.getMultipleSetting();
        filter.init();
        pageInfo.store();
        // 
        this.nextStep();
    }
    // 
    nextStep() {
        this.getIdList();
    }
    // 
    getIdListFinished() {
        // 
        this.resetGetIdListStatus();
        if (store.idList.length === 0) {
            return this.noResult();
        }
        log.log(lang.transl('_', store.idList.length.toString()));
        //  return 
        // return store.resetStates()
        if (store.idList.length <= this.ajaxThreadsDefault) {
            this.ajaxThreads = store.idList.length;
        }
        else {
            this.ajaxThreads = this.ajaxThreadsDefault;
        }
        for (let i = 0; i < this.ajaxThreads; i++) {
            this.getWorksData();
        }
    }
    // 
    async getWorksData(idData) {
        idData = idData || store.idList.shift();
        const id = idData.id;
        try {
            // 
            if (idData.type === 'novels') {
                const data = await API.getNovelData(id);
                await saveNovelData.save(data);
            }
            else {
                const data = await API.getArtworkData(id);
                await saveArtworkData.save(data);
            }
            this.afterGetWorksData();
        }
        catch (error) {
            //   response.ok 
            if (error.status) {
                this.logErrorStatus(error.status, id);
                this.afterGetWorksData();
            }
            else {
                // 
                console.log(error);
                setTimeout(() => {
                    this.getWorksData(idData);
                }, 2000);
            }
            return;
        }
    }
    // 
    afterGetWorksData() {
        this.logImagesNo();
        if (store.idList.length > 0) {
            // 
            this.getWorksData();
        }
        else {
            // 
            this.ajaxThreadsFinished++;
            if (this.ajaxThreadsFinished === this.ajaxThreads) {
                // 
                this.ajaxThreadsFinished = 0;
                this.crawlFinished();
            }
        }
    }
    // 
    crawlFinished() {
        if (store.result.length === 0) {
            return this.noResult();
        }
        this.sortResult();
        log.log(lang.transl('_', store.result.length.toString()));
        log.log(lang.transl('_'), 2);
        // 
        //  blob 
        // const type = [0, 0, 0, 0]
        // let blobSize = 0
        // for (const result of store.result) {
        //   type[result.type] = type[result.type] + 1
        //   if(result.novelBlob){
        //     blobSize += result.novelBlob.size
        //   }
        // }
        // console.log(type)
        // console.log(blobSize)
        // 
        //  EVT.InitiatorList[string]
        EVT.fire(EVT.events.crawlFinish);
    }
    // 
    logErrorStatus(status, id) {
        log.error(lang.transl('_2', id), 1);
        switch (status) {
            case 0:
                console.log(lang.transl('_0'));
                break;
            case 400:
                console.log(lang.transl('_400'));
                break;
            case 403:
                console.log(lang.transl('_403'));
                break;
            case 404:
                console.log(lang.transl('_404') + ' ' + id);
                break;
            default:
                break;
        }
    }
    // 
    logImagesNo() {
        log.log(lang.transl('_', store.result.length.toString()), 1, false);
    }
    //  0 
    noResult() {
        EVT.fire(EVT.events.crawlEmpty);
        titleBar.reset();
        log.error(lang.transl('_'), 2);
        window.alert(lang.transl('_'));
    }
    // 
    sortResult() { }
}
export { InitPageBase };
