import { EVT } from './EVT';
import { store } from './Store';
// 
class DownloadStates {
    constructor() {
        this.states = [];
        this.bindEvent();
    }
    bindEvent() {
        window.addEventListener(EVT.events.crawlFinish, async (ev) => {
            if (ev.detail.data.initiator !== EVT.InitiatorList.resume) {
                // 
                //  Resume 
                this.initList();
            }
        });
    }
    // 
    initList() {
        this.states = new Array(store.result.length).fill(-1);
    }
    // 
    downloadedCount() {
        let count = 0;
        const length = this.states.length;
        for (let i = 0; i < length; i++) {
            if (this.states[i] === 1) {
                count++;
            }
        }
        return count;
    }
    // 
    // 
    replace(states) {
        this.states = states;
    }
    // 
    // 
    resume() {
        const length = this.states.length;
        for (let i = 0; i < length; i++) {
            if (this.states[i] === 0) {
                this.setState(i, -1);
            }
        }
    }
    // 
    getFirstDownloadItem() {
        const length = this.states.length;
        for (let i = 0; i < length; i++) {
            if (this.states[i] === -1) {
                this.setState(i, 0);
                return i;
            }
        }
        return undefined;
    }
    // 
    setState(index, value) {
        this.states[index] = value;
    }
    clear() {
        this.states = [];
    }
}
const downloadStates = new DownloadStates();
export { downloadStates };
