import { API } from './API';
import { EVT } from './EVT';
import { store } from './Store';
//  tag
class PageInfo {
    constructor() {
        this.pageTitle = '';
        this.pageTag = '';
        this.getPageInfo();
        this.bindEvent();
    }
    get getPageTag() {
        return this.pageTag;
    }
    // 
    // 
    reset() {
        this.pageTitle = '';
        this.pageTag = '';
    }
    // 
    //  store 
    store() {
        this.getPageInfo();
        store.pageInfo.pageTitle = this.pageTitle;
        store.pageInfo.pageTag = this.pageTag;
    }
    // 
    getPageInfo() {
        this.reset();
        // 
        this.pageTitle = document.title
            .replace(/\[(||||||| )\] /, '')
            .replace(/^\(\d.*\) /, '');
        //  tag
        this.pageTag = decodeURIComponent(API.getTagFromURL(location.href));
    }
    bindEvent() {
        // 
        window.addEventListener(EVT.events.pageSwitch, () => {
            this.getPageInfo();
        });
        // 
        window.addEventListener(EVT.events.crawlFinish, async (ev) => {
            if (ev.detail.data.initiator === EVT.InitiatorList.resume) {
                this.store();
            }
        });
    }
}
const pageInfo = new PageInfo();
export { pageInfo };
