import { EVT } from './EVT';
import { store } from './Store';
import { log } from './Log';
import { lang } from './Lang';
import { downloadStates } from './DownloadStates';
import { IndexedDB } from './IndexedDB';
// 
class Resume {
    constructor() {
        this.flag = false; // 
        this.DBName = 'PBD';
        this.DBVer = 3;
        this.metaName = 'taskMeta'; // 
        this.dataName = 'taskData'; // 
        this.statesName = 'taskStates'; // 
        this.part = []; // 
        this.try = 0; // 
        // 
        // 
        //  1/2 100 w  100 w50 w25 w12.5 w  4  onceMax
        // 
        this.onceMax = 150000;
        this.putStatesTime = 2000; // 
        this.needPutStates = false; // 
        this.testSave = false; // 
        this.IDB = new IndexedDB();
        this.init();
    }
    async init() {
        if (location.hostname.includes('pixivision.net')) {
            return;
        }
        await this.initDB();
        !this.testSave && this.restoreData();
        this.bindEvent();
        this.regularPutStates();
        this.clearExired();
        this.testSave && this.test(1000000);
    }
    // 
    async initDB() {
        // 
        const onUpdate = (db) => {
            if (!db.objectStoreNames.contains(this.metaName)) {
                const metaStore = db.createObjectStore(this.metaName, {
                    keyPath: 'id',
                });
                metaStore.createIndex('id', 'id', { unique: true });
                metaStore.createIndex('url', 'url', { unique: true });
            }
            if (!db.objectStoreNames.contains(this.dataName)) {
                const dataStore = db.createObjectStore(this.dataName, {
                    keyPath: 'id',
                });
                dataStore.createIndex('id', 'id', { unique: true });
            }
            if (!db.objectStoreNames.contains(this.statesName)) {
                const statesStore = db.createObjectStore(this.statesName, {
                    keyPath: 'id',
                });
                statesStore.createIndex('id', 'id', { unique: true });
            }
        };
        // 
        return new Promise(async (resolve, reject) => {
            resolve(await this.IDB.open(this.DBName, this.DBVer, onUpdate));
        });
    }
    // 
    async restoreData() {
        // 
        if (!store.states.allowWork) {
            return;
        }
        // 1 
        const meta = (await this.IDB.get(this.metaName, this.getURL(), 'url'));
        if (!meta) {
            this.flag = false;
            return;
        }
        log.warning(lang.transl('_'));
        this.taskId = meta.id;
        // 2 
        //  id 
        const dataIdList = this.createIdList(meta.id, meta.part);
        // 
        const promiseList = [];
        for (const id of dataIdList) {
            promiseList.push(this.IDB.get(this.dataName, id));
        }
        await Promise.all(promiseList).then((res) => {
            store.result = [];
            const r = res;
            for (const taskData of r) {
                for (const data of taskData.data) {
                    store.result.push(data);
                }
            }
        });
        // 3 
        const data = (await this.IDB.get(this.statesName, this.taskId));
        if (data) {
            downloadStates.replace(data.states);
        }
        // 
        this.flag = true;
        log.success(lang.transl('_'), 2);
        // 
        EVT.fire(EVT.events.crawlFinish, {
            initiator: EVT.InitiatorList.resume,
        });
    }
    bindEvent() {
        // 
        window.addEventListener(EVT.events.crawlFinish, async (ev) => {
            if (ev.detail.data.initiator === EVT.InitiatorList.resume) {
                // 
                return;
            }
            // 
            const taskData = (await this.IDB.get(this.metaName, this.getURL(), 'url'));
            if (taskData) {
                await this.IDB.delete(this.metaName, taskData.id);
                await this.IDB.delete(this.statesName, taskData.id);
            }
            // 
            // 
            if (downloadStates.downloadedCount() === store.result.length) {
                return;
            }
            log.warning(lang.transl('_'));
            this.taskId = new Date().getTime();
            this.part = [];
            await this.saveTaskData();
            //  meta 
            const metaData = {
                id: this.taskId,
                url: this.getURL(),
                part: this.part.length,
            };
            this.IDB.add(this.metaName, metaData);
            //  states 
            const statesData = {
                id: this.taskId,
                states: downloadStates.states,
            };
            this.IDB.add(this.statesName, statesData);
            log.success(lang.transl('_'), 2);
        });
        // 
        window.addEventListener(EVT.events.downloadSucccess, () => {
            this.needPutStates = true;
        });
        // 
        window.addEventListener(EVT.events.downloadComplete, async () => {
            if (!this.taskId) {
                return;
            }
            const meta = (await this.IDB.get(this.metaName, this.taskId));
            if (!meta) {
                return;
            }
            this.IDB.delete(this.metaName, this.taskId);
            this.IDB.delete(this.statesName, this.taskId);
            const dataIdList = this.createIdList(this.taskId, meta.part);
            for (const id of dataIdList) {
                this.IDB.delete(this.dataName, id);
            }
            this.flag = false;
        });
        // 
        window.addEventListener(EVT.events.crawlStart, () => {
            this.flag = false;
        });
        // 
        window.addEventListener(EVT.events.pageSwitch, () => {
            this.flag = false;
            this.restoreData();
        });
    }
    //  put 
    async regularPutStates() {
        setInterval(() => {
            if (this.needPutStates) {
                const statesData = {
                    id: this.taskId,
                    states: downloadStates.states,
                };
                this.needPutStates = false;
                // 
                if (downloadStates.downloadedCount() === store.result.length) {
                    return;
                }
                this.IDB.put(this.statesName, statesData);
            }
        }, this.putStatesTime);
    }
    // 
    async saveTaskData() {
        return new Promise(async (resolve, reject) => {
            // (0.5  0  1)
            // 
            // 
            let tryNum = Math.floor(store.result.length * Math.pow(0.5, this.try));
            // 
            tryNum > this.onceMax && (tryNum = this.onceMax);
            let data = {
                id: this.numAppendNum(this.taskId, this.part.length),
                data: store.result.slice(this.getPartTotal(), this.getPartTotal() + tryNum),
            };
            try {
                // 
                await this.IDB.add(this.dataName, data);
                this.part.push(data.data.length); // 
                this.try = 0; // 
                // 
                if (this.getPartTotal() >= store.result.length) {
                    // console.log('add complete')
                    resolve();
                }
                else {
                    // 
                    resolve(this.saveTaskData());
                }
            }
            catch (error) {
                // 
                console.error(error);
                if (error.target && error.target.error && error.target.error.message) {
                    const msg = error.target.error.message;
                    if (msg.includes('too large')) {
                        // 
                        //  + 1 
                        this.try++;
                        resolve(this.saveTaskData());
                    }
                    else {
                        // 
                        this.try = 0;
                        log.error('IndexedDB: ' + msg);
                        reject(error);
                    }
                }
            }
        });
    }
    // 
    async clearExired() {
        //  30 30*24*60*60*1000
        const expiryTime = 2592000000;
        const nowTime = new Date().getTime();
        const callback = (item) => {
            if (item) {
                const data = item.value;
                // 
                if (nowTime - data.id > expiryTime) {
                    this.IDB.delete(this.metaName, data.id);
                    this.IDB.delete(this.statesName, data.id);
                    const dataIdList = this.createIdList(data.id, data.part);
                    for (const id of dataIdList) {
                        this.IDB.delete(this.dataName, id);
                    }
                }
                item.continue();
            }
        };
        this.IDB.openCursor(this.metaName, callback);
    }
    //  part 
    getPartTotal() {
        if (this.part.length === 0) {
            return 0;
        }
        return this.part.reduce((prev, curr) => {
            return prev + curr;
        });
    }
    //  url
    getURL() {
        return window.location.href.split('#')[0];
    }
    // 
    //  task id  (part)
    numAppendNum(id, num) {
        return parseInt(id.toString() + num);
    }
    //  taskMeta  id  part  taskData  id 
    createIdList(taskid, part) {
        // part  1  0 
        //  id  0 
        const arr = [];
        let start = 0;
        while (start < part) {
            arr.push(this.numAppendNum(taskid, start));
            start++;
        }
        return arr;
    }
    // 
    //  id 
    test(num) {
        while (num > 0) {
            store.result.push({
                bmk: 1644,
                bookmarked: false,
                date: '2020-07-11',
                dlCount: 1,
                ext: 'jpg',
                fullHeight: 1152,
                fullWidth: 2048,
                id: '82900613_p0',
                idNum: 82900613,
                novelBlob: null,
                pageCount: 1,
                rank: '',
                seriesOrder: '',
                seriesTitle: '',
                tags: [
                    '',
                    'YouTuber',
                    '',
                    '',
                    '',
                    '',
                ],
                tagsTranslated: [
                    '',
                    '',
                    'YouTuber',
                    'YouTuber',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                ],
                thumb: 'https://i.pximg.net/c/250x250_80_a2/custom-thumb/img/2020/07/11/17/05/41/82900613_p0_custom1200.jpg',
                title: '',
                type: 0,
                ugoiraInfo: null,
                url: 'https://i.pximg.net/img-original/img/2020/07/11/17/05/41/82900613_p0.jpg',
                user: '',
                userId: '10852879',
            });
            num--;
        }
        EVT.fire(EVT.events.crawlFinish);
    }
}
const resume = new Resume();
export { resume };
