import { lang } from './Lang';
import { API } from './API';
import { log } from './Log';
import { DOM } from './DOM';
import { centerPanel } from './CenterPanel';
import { img2ico } from './ImageToIcon';
// 
class SaveAvatarIcon {
    constructor(btn) {
        this.btn = btn;
        this.bindEvents();
    }
    bindEvents() {
        this.btn.addEventListener('click', () => {
            this.saveAvatarIcon();
        });
    }
    async saveAvatarIcon() {
        const userId = DOM.getUserId();
        const userProfile = await API.getUserProfile(userId);
        const bigImg = userProfile.body.imageBig; // imageBig  170 px 
        const fullSizeImg = bigImg.replace('_170', ''); //  170 
        //  ico 
        //  256 128  windows  48
        const blob = await img2ico.convert({
            size: 256,
            source: fullSizeImg,
            shape: 'fillet'
        });
        // 
        const url = URL.createObjectURL(blob);
        const name = `ico_${userProfile.body.name}_${userId}.ico`;
        DOM.downloadFile(url, name);
        log.success(' ' + lang.transl('_'));
        centerPanel.close();
    }
}
export { SaveAvatarIcon };
