// 
class BlackAndWhiteImage {
    constructor() {
        this.latitude = 1; // 
    }
    async check(imgUrl) {
        const img = await this.loadImg(imgUrl).catch((error) => {
            console.log(error);
        });
        // 
        if (!img) {
            return false;
        }
        const first = this.getResult(this.getColor(img));
        return first;
        // 
        // 
    }
    // 
    async loadImg(url) {
        return new Promise(async (resolve, reject) => {
            const img = document.createElement('img');
            img.onload = () => resolve(img);
            img.onerror = () => {
                reject(new Error(`Load image error! url: ${url}`));
            };
            //  blobURL 
            if (url.startsWith('blob')) {
                img.src = url;
            }
            else {
                const res = await fetch(url).catch((error) => {
                    throw new Error(`Load image error! url: ${url}`);
                });
                const blob = await res.blob();
                const blobURL = URL.createObjectURL(blob);
                img.src = blobURL;
            }
        });
    }
    //  rgb 
    getColor(img) {
        const width = img.width;
        const height = img.height;
        const canvas = document.createElement('canvas');
        canvas.width = width;
        canvas.height = height;
        const con = canvas.getContext('2d');
        con.drawImage(img, 0, 0);
        const imageData = con.getImageData(0, 0, width, height);
        const data = imageData.data;
        let r = 0;
        let g = 0;
        let b = 0;
        // 
        for (let row = 0; row < height; row++) {
            for (let col = 0; col < width; col++) {
                r += data[(width * row + col) * 4];
                g += data[(width * row + col) * 4 + 1];
                b += data[(width * row + col) * 4 + 2];
            }
        }
        // 
        r /= width * height;
        g /= width * height;
        b /= width * height;
        // 
        r = Math.round(r);
        g = Math.round(g);
        b = Math.round(b);
        return [r, g, b];
    }
    //  rgb 
    getResult(rgb) {
        const [r, g, b] = rgb;
        //  rgb 
        if (r === g && g === b) {
            return true;
        }
        else {
            //  rgb 
            //  rgb  rgb  1
            const max = Math.max(r, g, b); //  rgb 
            const min = max - this.latitude; // 
            //  rgb 
            return [r, g, b].every((number) => {
                return number >= min;
            });
        }
    }
}
const blackAndWhiteImage = new BlackAndWhiteImage();
export { blackAndWhiteImage };
