// 
import { InitPageBase } from './InitPageBase';
import { Colors } from './Colors';
import { lang } from './Lang';
import { options } from './Options';
import { API } from './API';
import { store } from './Store';
import { log } from './Log';
import { DOM } from './DOM';
import { pageInfo } from './PageInfo';
import { SaveAvatarIcon } from './SaveAvatarIcon';
class InitUserPage extends InitPageBase {
    constructor() {
        super();
        this.tag = ''; //  tag
        this.listType = 0; // 
        this.onceNumber = 48; //  48  24 
        this.init();
    }
    // 
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
        const btn = DOM.addBtn('otherBtns', Colors.green, lang.transl('_'), [
            ['title', lang.transl('_')],
        ]);
        new SaveAvatarIcon(btn);
    }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: lang.transl('_1'),
            value: '-1',
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
    }
    nextStep() {
        this.readyGetIdList();
    }
    readyGetIdList() {
        // 
        //  pathname  id 
        const test = location.pathname.match(/\/users\/\d+(\/.+)/);
        if (test === null) {
            // 
            this.listType = 0;
        }
        else if (test.length === 2) {
            const str = test[1]; // id 
            if (str.includes('/artworks')) {
                // 
                this.listType = 1;
            }
            else if (str.includes('/illustrations')) {
                // 
                this.listType = 2;
            }
            else if (str.includes('/manga')) {
                // 
                this.listType = 3;
            }
            else if (str.includes('/novels')) {
                // 
                this.listType = 4;
                this.onceNumber = 24; //  24 
            }
        }
        ;
        (this.tag = pageInfo.getPageTag) ? this.getIdListByTag() : this.getIdList();
        log.log(lang.transl('_'));
    }
    getOffset() {
        const nowPage = API.getURLSearchField(location.href, 'p'); //  1 
        let offset = 0;
        if (nowPage) {
            offset = (parseInt(nowPage) - 1) * this.onceNumber;
        }
        if (offset < 0) {
            offset = 0;
        }
        return offset;
    }
    // 
    getRequsetNumber() {
        let requsetNumber = 9999999;
        if (this.crawlNumber !== -1) {
            requsetNumber = this.onceNumber * this.crawlNumber;
        }
        return requsetNumber;
    }
    //  id 
    async getIdList() {
        let type = [];
        switch (this.listType) {
            case 0:
                type = ['illusts', 'manga', 'novels'];
                break;
            case 1:
                type = ['illusts', 'manga'];
                break;
            case 2:
                type = ['illusts'];
                break;
            case 3:
                type = ['manga'];
                break;
            case 4:
                type = ['novels'];
                break;
        }
        let idList = await API.getUserWorksByType(DOM.getUserId(), type);
        //  24 
        const allWorkIsNovels = idList.every((val) => {
            return val.type === 'novels';
        });
        allWorkIsNovels && (this.onceNumber = 24);
        // 
        const offset = this.getOffset();
        const requsetNumber = this.getRequsetNumber();
        //  id 
        idList.sort(API.sortByProperty('id')).reverse();
        //  tag  API 
        //  offset  id
        idList.splice(idList.length - offset, idList.length);
        //  requsetNumber  id
        if (idList.length > requsetNumber) {
            idList.splice(0, idList.length - requsetNumber);
        }
        // 
        store.idList = store.idList.concat(idList);
        this.getIdListFinished();
    }
    //  id  tag
    async getIdListByTag() {
        //  0  tag
        let flag = 'illustmanga';
        switch (this.listType) {
            case 1:
                flag = 'illustmanga';
                break;
            case 2:
                flag = 'illusts';
                break;
            case 3:
                flag = 'manga';
                break;
            case 4:
                flag = 'novels';
                break;
        }
        // 
        const offset = this.getOffset();
        const requsetNumber = this.getRequsetNumber();
        let data = await API.getUserWorksByTypeWithTag(DOM.getUserId(), flag, this.tag, offset, requsetNumber);
        //  illustType 
        if (this.listType === 4) {
            const d = data;
            d.body.works.forEach((data) => store.idList.push({
                type: 'novels',
                id: data.id,
            }));
        }
        else {
            const d = data;
            d.body.works.forEach((data) => {
                let type = 'illusts';
                switch (data.illustType) {
                    case 0:
                        type = 'illusts';
                        break;
                    case 1:
                        type = 'manga';
                        break;
                    case 2:
                        type = 'ugoira';
                        break;
                }
                store.idList.push({
                    type,
                    id: data.id,
                });
            });
        }
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.tag = '';
        this.listType = 0;
        this.listPageFinished = 0;
    }
    sortResult() {
        //  id id 
        store.result.sort(API.sortByProperty('id'));
    }
}
export { InitUserPage };
