//   artwork 
import { InitPageBase } from '../InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { options } from '../Options';
import { filter } from '../Filter';
import { API } from '../API';
import { store } from '../Store';
import { log } from '../Log';
import { DOM } from '../DOM';
class InitNewArtworkPage extends InitPageBase {
    constructor() {
        super();
        this.option = this.resetOption();
        this.limitMax = 20; //  20
        this.fetchCount = 0; // 
        this.init();
    }
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    appendElseEl() { }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: `1 - ${this.maxCount}`,
            value: '100',
        });
    }
    getWantPage() {
        const check = this.checkWantPageInputGreater0();
        if (check == undefined) {
            return;
        }
        this.crawlNumber = check;
        if (this.crawlNumber > this.maxCount) {
            this.crawlNumber = this.maxCount;
        }
        log.warning(lang.transl('_', this.crawlNumber.toString()));
    }
    nextStep() {
        this.initFetchURL();
        this.getIdList();
    }
    resetOption() {
        return {
            lastId: '0',
            limit: '20',
            type: '',
            r18: '',
        };
    }
    //  url
    initFetchURL() {
        this.option = this.resetOption();
        if (this.crawlNumber < this.limitMax) {
            this.option.limit = this.crawlNumber.toString();
        }
        else {
            this.option.limit = this.limitMax.toString();
        }
        //  illust
        this.option.type = API.getURLSearchField(location.href, 'type') || 'illust';
        //  R18 
        this.option.r18 = (location.href.includes('_r18.php') || false).toString();
    }
    async getIdList() {
        let data;
        try {
            data = await API.getNewIllustData(this.option);
        }
        catch (error) {
            this.getIdList();
            return;
        }
        let useData = data.body.illusts;
        for (const nowData of useData) {
            // 
            if (this.fetchCount + 1 > this.crawlNumber) {
                break;
            }
            else {
                this.fetchCount++;
            }
            // 
            if (nowData.isAdContainer) {
                continue;
            }
            const filterOpt = {
                id: nowData.illustId,
                width: nowData.width,
                height: nowData.height,
                pageCount: nowData.pageCount,
                bookmarkData: nowData.bookmarkData,
                illustType: nowData.illustType,
                tags: nowData.tags,
            };
            if (await filter.check(filterOpt)) {
                store.idList.push({
                    type: API.getWorkType(nowData.illustType),
                    id: nowData.illustId,
                });
            }
        }
        log.log(lang.transl('_', this.fetchCount.toString()), 1, false);
        // 
        if (this.fetchCount >= this.crawlNumber ||
            this.fetchCount >= this.maxCount) {
            log.log(lang.transl('_'));
            this.getIdListFinished();
            return;
        }
        // 
        this.option.lastId = data.body.lastId;
        this.getIdList();
    }
    resetGetIdListStatus() {
        this.fetchCount = 0;
    }
}
export { InitNewArtworkPage };
