// 
import { EVT } from './EVT';
// 
class Store {
    constructor() {
        this.idList = []; //  id
        this.resultMeta = []; // 
        // 
        this.resultIDList = []; //  id 
        // resultIDList  id
        this.result = []; // 
        this.rankList = {}; // 
        // 
        this.states = {
            allowWork: true,
            quickDownload: false,
            notAutoDownload: false,
        };
        // 
        this.pageInfo = {
            pageTitle: '',
            pageTag: '',
        };
        const allowWorkTrue = [
            EVT.events.crawlFinish,
            EVT.events.crawlEmpty,
            EVT.events.crawlError,
            EVT.events.downloadPause,
            EVT.events.downloadStop,
        ];
        allowWorkTrue.forEach((type) => {
            window.addEventListener(type, () => {
                this.states.allowWork = true;
            });
        });
        const allowWorkFalse = [EVT.events.crawlStart, EVT.events.downloadStart];
        allowWorkFalse.forEach((type) => {
            window.addEventListener(type, () => {
                this.states.allowWork = false;
            });
        });
        window.addEventListener(EVT.events.crawlStart, () => {
            this.resetResult();
        });
        window.addEventListener(EVT.events.downloadComplete, () => {
            this.resetStates();
        });
    }
    /*
     id - id +  44920385_p0 id 
     url -  url
     title - 
     tags -  tag 
     tagsTranslated -  tag  tag
     user - 
     userid - id
     fullWidth - 
     fullHeight - 
     ext - 
     bmk - 
     date -  yyyy-MM-dd 2019-08-29
     type -  illustrationmangaugoira
     rank - 
     ugoiraInfo -  frames mimeTypestring
     */
    assignResult(data) {
        // 
        const dataDefault = {
            idNum: 0,
            id: '',
            url: '',
            thumb: '',
            title: '',
            pageCount: 1,
            dlCount: 1,
            tags: [],
            tagsTranslated: [],
            user: '',
            userId: '',
            fullWidth: 0,
            fullHeight: 0,
            ext: '',
            bmk: 0,
            bookmarked: false,
            date: '',
            type: 0,
            rank: '',
            ugoiraInfo: null,
            seriesTitle: null,
            seriesOrder: null,
            novelBlob: null,
        };
        return Object.assign(dataDefault, data);
    }
    // 
    addResult(data) {
        // 
        if (data.idNum !== undefined && this.resultIDList.includes(data.idNum)) {
            return;
        }
        if (data.idNum !== undefined) {
            this.resultIDList.push(data.idNum);
        }
        // 
        const result = this.assignResult(data);
        this.resultMeta.push(result);
        EVT.fire(EVT.events.addResult, result);
        if (result.type === 3) {
            this.result.push(result);
        }
        else {
            // 
            for (let i = 0; i < result.dlCount; i++) {
                const result = this.assignResult(data);
                result.idNum = parseInt(result.id);
                result.id = result.id + `_p${i}`;
                result.url = result.url.replace('p0', 'p' + i);
                this.result.push(result);
            }
        }
    }
    getRankList(index) {
        return this.rankList[index];
    }
    setRankList(id, rank) {
        this.rankList[id] = rank;
    }
    resetResult() {
        this.resultMeta = [];
        this.resultIDList = [];
        this.result = [];
        this.idList = [];
        this.rankList = {};
    }
    resetStates() {
        this.states.allowWork = true;
        this.states.quickDownload = false;
    }
}
const store = new Store();
export { store };
