import { EVT } from './EVT';
import { DOM } from './DOM';
import { pageInfo } from './PageInfo';
import { themeColor } from './ThemeColor';
// 
class FastScreen {
    constructor() {
        this.favNums = [
            '100users',
            '500users',
            '1000users',
            '3000users',
            '5000users',
            '10000users',
            '20000users',
            '30000users',
            '50000users',
        ]; // 200  2000 40000 
        this.create();
        window.addEventListener(EVT.events.pageTypeChange, () => {
            this.destroy();
        });
    }
    // 
    create() {
        // 
        const target = document.querySelector('#root>div');
        if (!target) {
            setTimeout(() => {
                this.create();
            }, 300);
            return;
        }
        const fastScreenArea = document.createElement('div');
        fastScreenArea.className = 'fastScreenArea';
        this.favNums.forEach((secondTag) => {
            const a = document.createElement('a');
            a.innerText = secondTag;
            a.href = 'javascript:viod(0)';
            a.onclick = () => {
                this.openFastScreenLink(secondTag);
            };
            fastScreenArea.appendChild(a);
        });
        themeColor.register(fastScreenArea);
        target.insertAdjacentElement('afterend', fastScreenArea);
    }
    // 
    openFastScreenLink(secondTag) {
        //  tag tag
        const firstTag = pageInfo.getPageTag.split(' ')[0];
        const fullTag = encodeURIComponent(firstTag + ' ' + secondTag);
        //  tag  tag
        const newURL = location.href.replace(encodeURIComponent(pageInfo.getPageTag), fullTag);
        //  s_mode=s_tag 
        const u = new URL(newURL);
        u.searchParams.set('s_mode', 's_tag');
        location.href = u.toString();
    }
    destroy() {
        // 
        const fastScreen = document.querySelector('.fastScreenArea');
        DOM.removeEl(fastScreen);
    }
}
export { FastScreen };
