// 
import { InitPageBase } from './InitPageBase';
import { Colors } from './Colors';
import { lang } from './Lang';
import { options } from './Options';
import { API } from './API';
import { store } from './Store';
import { log } from './Log';
import { DOM } from './DOM';
class InitFollowingPage extends InitPageBase {
    constructor() {
        super();
        this.baseOffset = 0; // 
        this.onceNumber = 24; //  24 
        this.getUserListNo = 0; // 
        this.limit = 100; // 
        this.totalNeed = Number.MAX_SAFE_INTEGER;
        this.myId = '';
        this.rest = 'show';
        this.userList = [];
        this.index = 0; // getIdList  userList 
        this.init();
    }
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: lang.transl('_1'),
            value: '-1',
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
    }
    nextStep() {
        this.readyGet();
        log.log(lang.transl('_'));
        this.getUserList();
    }
    readyGet() {
        this.rest = location.href.includes('rest=hide') ? 'hide' : 'show';
        // 
        const nowPage = API.getURLSearchField(location.href, 'p');
        // 
        if (nowPage !== '') {
            this.baseOffset = (parseInt(nowPage) - 1) * this.onceNumber;
        }
        else {
            this.baseOffset = 0;
        }
        // 
        this.totalNeed = Number.MAX_SAFE_INTEGER;
        if (this.crawlNumber !== -1) {
            this.totalNeed = this.onceNumber * this.crawlNumber;
        }
        //  id
        const test = /users\/(\d*)\//.exec(location.href);
        if (test && test.length > 1) {
            this.myId = test[1];
        }
        else {
            const msg = `Get the user's own id failed`;
            log.error(msg, 2);
            throw new Error(msg);
        }
    }
    //  id
    async getUserList() {
        const offset = this.baseOffset + this.getUserListNo * this.limit;
        let res;
        try {
            res = await API.getFollowingList(this.myId, this.rest, offset);
        }
        catch (_a) {
            this.getUserList();
            return;
        }
        const users = res.body.users;
        if (users.length === 0) {
            // 
            return this.getUserListComplete();
        }
        for (const userData of users) {
            this.userList.push(userData.userId);
            if (this.userList.length >= this.totalNeed) {
                // 
                return this.getUserListComplete();
            }
        }
        log.log(lang.transl('_x', this.userList.length.toString()), 1, false);
        this.getUserListNo++;
        this.getUserList();
    }
    getUserListComplete() {
        log.log(lang.transl('_x', this.userList.length.toString()));
        if (this.userList.length === 0) {
            return this.getIdListFinished();
        }
        this.getIdList();
    }
    //  id 
    async getIdList() {
        let idList = [];
        try {
            idList = await API.getUserWorksByType(this.userList[this.index]);
        }
        catch (_a) {
            this.getIdList();
            return;
        }
        store.idList = store.idList.concat(idList);
        this.index++;
        if (this.index >= this.userList.length) {
            return this.getIdListFinished();
        }
        log.log(lang.transl('_', store.idList.length.toString()), 1, false);
        this.getIdList();
    }
    resetGetIdListStatus() {
        this.listPageFinished = 0;
        this.userList = [];
        this.getUserListNo = 0;
        this.index = 0;
    }
    sortResult() {
        //  id id 
        store.result.sort(API.sortByProperty('id'));
    }
}
export { InitFollowingPage };
