// 
import { InitPageBase } from './InitPageBase';
import { Colors } from './Colors';
import { lang } from './Lang';
import { centerPanel } from './CenterPanel';
import { options } from './Options';
import { DOM } from './DOM';
import { store } from './Store';
import { log } from './Log';
class InitIndexPage extends InitPageBase {
    constructor() {
        super();
        this.ready = false;
        this.downIdButton = document.createElement('button');
        this.downIdInput = document.createElement('textarea');
        this.init();
    }
    appendCenterBtns() {
        this.downIdButton = DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_id'), [['id', 'down_id_button']]);
    }
    appendElseEl() {
        // id
        this.downIdInput.id = 'down_id_input';
        this.downIdInput.style.display = 'none';
        this.downIdInput.setAttribute('placeholder', lang.transl('_id'));
        DOM.insertToHead(this.downIdInput);
    }
    setFormOption() {
        options.hideOption([1]);
    }
    initElse() {
        this.downIdButton.addEventListener('click', () => {
            if (!this.ready) {
                // 
                centerPanel.close();
                this.downIdInput.style.display = 'block';
                this.downIdInput.focus();
                document.documentElement.scrollTop = 0;
            }
            else {
                this.readyCrawl();
            }
        }, false);
        // 
        this.downIdInput.addEventListener('change', () => {
            if (this.downIdInput.value !== '') {
                this.ready = true;
                centerPanel.show();
                this.downIdButton.textContent = lang.transl('_');
            }
            else {
                this.ready = false;
                centerPanel.close();
                this.downIdButton.textContent = lang.transl('_id');
            }
        });
    }
    nextStep() {
        // id
        log.log(lang.transl('_'));
        this.getIdList();
    }
    getWantPage() { }
    getIdList() {
        //  id 
        const type = window.location.pathname === '/novel/' ? 'novels' : 'unknown';
        //  id
        const tempSet = new Set(this.downIdInput.value.split('\n'));
        const idValue = Array.from(tempSet);
        for (const id of idValue) {
            //  id 
            const nowId = parseInt(id);
            if (isNaN(nowId) || nowId < 22 || nowId > 99999999) {
                log.error(lang.transl('_id'), 0, false);
            }
            else {
                store.idList.push({
                    type: type,
                    id: nowId.toString(),
                });
            }
        }
        this.getIdListFinished();
    }
    resetGetIdListStatus() { }
    destroy() {
        DOM.clearSlot('crawlBtns');
        DOM.clearSlot('otherBtns');
        DOM.removeEl(this.downIdInput);
    }
}
export { InitIndexPage };
